:root {
  --mainWhite: #ffffff;
  --mainWhiteOpc: rgba(255, 255, 255, 0.2);
  --mainWhiteOpc2: rgba(255, 255, 255, 0.7);
  --mainBlack: rgb(31, 31, 31);
  --mainBlackOpc: rgba(31, 31, 31, 0.5);
  --mainGreen: #29e8ce;
  --mainGreenOpc2: rgba(41, 232, 206, 0.2);
  --mainGreenOpc3: rgba(41, 232, 206, 0.1);
  --mainGreenOpc: rgba(41, 232, 206, 0.02);
  --mainGray: rgba(255, 255, 255, 0.8);
  --opc: 0.6;
  --breakpoint-xs: 0;
  --breakpoint-sm: 576px;
  --breakpoint-md: 768px;
  --breakpoint-lg: 992px;
  --breakpoint-xl: 1200px;
}

body {
  padding: 0 !important;
  margin: 0 !important;
  min-height: 100vh;
  overflow: hidden;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.principal {
  width: 100vw;
  min-height: 100vh;
  background-image: url("./components/img/background.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

li {
  list-style: none !important;
}

a {
  color: var(--mainWhite) !important;
}

a:hover {
  text-decoration: none !important;
}

.container {
  max-width: 100% !important;
}

input,
textarea {
  border-color: var(--mainGreen) !important;
  color: var(--mainGray) !important;
  border-radius: 0 !important;
  background: transparent !important;
}

input:focus,
textarea:focus {
  color: var(--mainGray) !important;
}

@media screen and (max-width: 767px) {
  body {
    overflow-y: scroll !important;
    /*firefox*/
    scrollbar-width: thin !important;
    scrollbar-color: var(--mainGreen) var(--mainBlack) !important;
  }
  body::-webkit-scrollbar {
    max-width: 0.4rem !important;
  }

  body::-webkit-scrollbar-track {
    background: var(--mainBlack) !important;
  }

  body::-webkit-scrollbar-thumb {
    background-color: var(--mainGreen) !important;
  }
  .no-p {
    padding: 0 !important;
  }
}
@media screen and (max-width: 412px) {
  .col-2-1 {
    padding-left: 0 !important;
    padding-right: 0 !important;
    margin-left: 0 !important;
    margin-right: 0 !important;
  }
}
@media screen and (max-width: 768px) {
  body {
    word-break: break-all;
  }
}
